BAIJ,6BACI,CAAA,0BACA,CAAA,eACA,CAAA,iBACA,CAAA,cAIJ,cACI,CAAA,+BACA,CAAA,qCACA,CAAA,8BACA,CAAA,aACA,CAAA,YACA,CAAA,eACA,CAAA,cACA,CAAA,kCACA,CAAA,iCAEA,gBACI,CAAA,cACA,CAAA,8DACA,CAAA,uBACA,CAAA,uCAEA,+BACI,CAAA,6BACA,CAAA,0BAMZ,iBACI,UACI,CAAA,WACA,CAAA,WAGJ,WACI,CAAA,aAGJ,oCACI,CAAA,CAAA,yBAIR,KACI,yBACI,CAAA,iBAGJ,WACI,CAAA,YACA,CAAA,eACA,CAAA,WACA,CAAA,mCAEA,qBACI,CAAA,4BAGJ,UACI,CAAA,YACA,CAAA,iBACA,CAAA,4CACA,CAAA,kCAGJ,MACI,CAAA,6BAIR,qBACI,CAAA,kBAGJ,qBACI,CAAA,gCACA,CAAA,aAGJ,oCACI,CAAA,CAAA,yBAIR,MACI,kBACI,CAAA,iBACA,CAAA,iBAGJ,WACI,CAAA,2BACA,CAAA,+BAEA,cACI,CAAA,gBAIR,WACI,CAAA,2BACA,CAAA,8BAEA,cACI,CAAA,cAIR,WACI,CAAA,2BACA,CAAA,sBAEA,cACI,CAAA,CAAA,+BAMZ,MACI,wBACI,CAAA,0BACA,CAAA,2BACA,CAAA,wBACA,CAAA,qBACA,CAAA,CAAA,uCAKR,EACI,mCACI,CAAA,sCACA,CAAA,oCACA,CAAA,CAAA,aAKR,iBACI,qBACI,CAAA,eACA,CAAA,eACA,CAAA,UACA,CAAA,yCAGJ,uBAGI,CAAA","sourcesContent":["// Kali Linux Terminal Portfolio - Main Styles\r\n// CSS Custom Properties (Theme Variables)\r\n:root {\r\n    // Kali Linux Color Palette\r\n    --color-primary: #00FF41;      // Matrix Green\r\n    --color-secondary: #39FF14;    // Neon Green\r\n    --color-background: #0D1117;   // GitHub Dark\r\n    --color-surface: #161B22;      // Dark Surface\r\n    --color-text: #F0F6FC;         // Primary Text\r\n    --color-text-secondary: #8B949E; // Secondary Text\r\n    --color-accent: #FF6B35;       // Orange Accent\r\n    --color-warning: #FFB800;      // Warning Yellow\r\n    --color-error: #FF4444;        // Error Red\r\n    --color-success: #00FF41;      // Success Green\r\n\r\n    // Typography\r\n    --font-primary: 'JetBrains Mono', 'Fira Code', monospace;\r\n    --font-mono: 'Ubuntu Mono', 'Courier New', monospace;\r\n\r\n    // Spacing & Layout\r\n    --spacing-xs: 4px;\r\n    --spacing-sm: 8px;\r\n    --spacing-md: 16px;\r\n    --spacing-lg: 24px;\r\n    --spacing-xl: 32px;\r\n    --spacing-xxl: 48px;\r\n\r\n    // Border Radius\r\n    --radius-sm: 4px;\r\n    --radius-md: 8px;\r\n    --radius-lg: 12px;\r\n\r\n    // Shadows & Effects\r\n    --shadow-glow: 0 0 20px rgba(0, 255, 65, 0.3);\r\n    --shadow-terminal: 0 8px 32px rgba(0, 0, 0, 0.8);\r\n    --shadow-elevation: 0 4px 16px rgba(0, 0, 0, 0.6);\r\n\r\n    // Animation Timing\r\n    --duration-fast: 150ms;\r\n    --duration-normal: 300ms;\r\n    --duration-slow: 500ms;\r\n    --easing: cubic-bezier(0.4, 0, 0.2, 1);\r\n}\r\n\r\n// Global Reset & Base Styles\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nhtml {\r\n    height: 100%;\r\n    font-size: 16px;\r\n}\r\n\r\nbody {\r\n    height: 100%;\r\n    font-family: var(--font-primary);\r\n    background: var(--color-background);\r\n    color: var(--color-text);\r\n    overflow: hidden;\r\n    line-height: 1.5;\r\n    -webkit-font-smoothing: antialiased;\r\n    -moz-osx-font-smoothing: grayscale;\r\n}\r\n\r\n// Matrix Background Canvas\r\n#matrix-bg {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100vw;\r\n    height: 100vh;\r\n    z-index: -1;\r\n    pointer-events: none;\r\n    opacity: 0.8;\r\n}\r\n\r\n// Loading Screen\r\n.loading-screen {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: var(--color-background);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    z-index: 9999;\r\n    font-family: var(--font-mono);\r\n\r\n    .loading-message {\r\n        font-size: 14px;\r\n        color: var(--color-text-secondary);\r\n        margin-top: 10px;\r\n        min-height: 20px;\r\n    }\r\n}\r\n\r\n// Terminal Cursor Animation\r\n@keyframes blink {\r\n    0%, 50% { opacity: 1; }\r\n    51%, 100% { opacity: 0; }\r\n}\r\n\r\n.terminal-cursor,\r\n.cursor,\r\n.cursor-blink {\r\n    animation: blink 1s infinite;\r\n    color: var(--color-primary);\r\n}\r\n\r\n// Main App Container\r\n#app {\r\n    height: 100vh;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: var(--spacing-lg);\r\n\r\n    &.mobile-layout {\r\n        padding: var(--spacing-sm);\r\n    }\r\n}\r\n\r\n// Terminal Window\r\n.terminal-window {\r\n    width: 95vw;\r\n    height: 90vh;\r\n    max-width: 1600px;\r\n    max-height: 1000px;\r\n    background: var(--color-surface);\r\n    border: 2px solid var(--color-primary);\r\n    border-radius: var(--radius-md);\r\n    box-shadow: var(--shadow-terminal);\r\n    display: flex;\r\n    flex-direction: column;\r\n    position: relative;\r\n    overflow: hidden;\r\n    backdrop-filter: blur(10px);\r\n\r\n    &::before {\r\n        content: '';\r\n        position: absolute;\r\n        top: 0;\r\n        left: 0;\r\n        right: 0;\r\n        bottom: 0;\r\n        background: linear-gradient(\r\n            135deg,\r\n            rgba(0, 255, 65, 0.1) 0%,\r\n            transparent 50%,\r\n            rgba(0, 255, 65, 0.05) 100%\r\n        );\r\n        pointer-events: none;\r\n        z-index: 1;\r\n    }\r\n\r\n    // Terminal Header\r\n    .terminal-header {\r\n        height: 40px;\r\n        background: var(--color-background);\r\n        border-bottom: 1px solid var(--color-primary);\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: space-between;\r\n        padding: 0 var(--spacing-md);\r\n        user-select: none;\r\n        cursor: move;\r\n        position: relative;\r\n        z-index: 2;\r\n\r\n        .window-controls {\r\n            display: flex;\r\n            gap: var(--spacing-sm);\r\n\r\n            .control {\r\n                width: 12px;\r\n                height: 12px;\r\n                border-radius: 50%;\r\n                cursor: pointer;\r\n                transition: all var(--duration-fast) var(--easing);\r\n\r\n                &.close {\r\n                    background: #FF5F56;\r\n                    &:hover { background: #FF3333; box-shadow: 0 0 8px rgba(255, 51, 51, 0.5); }\r\n                }\r\n\r\n                &.minimize {\r\n                    background: #FFBD2E;\r\n                    &:hover { background: #FFB800; box-shadow: 0 0 8px rgba(255, 184, 0, 0.5); }\r\n                }\r\n\r\n                &.maximize {\r\n                    background: #27CA3F;\r\n                    &:hover { background: #00FF41; box-shadow: 0 0 8px rgba(0, 255, 65, 0.5); }\r\n                }\r\n            }\r\n        }\r\n\r\n        .window-title {\r\n            font-family: var(--font-mono);\r\n            font-size: 14px;\r\n            color: var(--color-primary);\r\n            font-weight: 500;\r\n        }\r\n    }\r\n\r\n    // Terminal Content Area\r\n    .terminal-content {\r\n        flex: 1;\r\n        display: flex;\r\n        min-height: 0;\r\n        position: relative;\r\n        z-index: 2;\r\n    }\r\n}\r\n\r\n// File Tree Sidebar\r\n.file-tree {\r\n    width: 300px;\r\n    background: rgba(13, 17, 23, 0.9);\r\n    border-right: 1px solid var(--color-primary);\r\n    display: flex;\r\n    flex-direction: column;\r\n    overflow: hidden;\r\n\r\n    .tree-header {\r\n        padding: var(--spacing-md);\r\n        border-bottom: 1px solid rgba(0, 255, 65, 0.3);\r\n        display: flex;\r\n        align-items: center;\r\n        gap: var(--spacing-sm);\r\n        font-weight: 600;\r\n        color: var(--color-primary);\r\n\r\n        .folder-icon {\r\n            font-size: 18px;\r\n        }\r\n    }\r\n\r\n    .tree-content {\r\n        flex: 1;\r\n        overflow-y: auto;\r\n        padding: var(--spacing-sm) 0;\r\n\r\n        &::-webkit-scrollbar {\r\n            width: 6px;\r\n        }\r\n\r\n        &::-webkit-scrollbar-track {\r\n            background: rgba(0, 0, 0, 0.2);\r\n        }\r\n\r\n        &::-webkit-scrollbar-thumb {\r\n            background: var(--color-primary);\r\n            border-radius: 3px;\r\n            \r\n            &:hover {\r\n                background: var(--color-secondary);\r\n            }\r\n        }\r\n    }\r\n\r\n    // Tree Items\r\n    .tree-item {\r\n        position: relative;\r\n        cursor: pointer;\r\n        transition: all var(--duration-fast) var(--easing);\r\n        font-family: var(--font-mono);\r\n        font-size: 13px;\r\n\r\n        &:hover {\r\n            background: rgba(0, 255, 65, 0.1);\r\n            \r\n            .tree-item-content {\r\n                color: var(--color-secondary);\r\n            }\r\n        }\r\n\r\n        &.selected {\r\n            background: rgba(0, 255, 65, 0.2);\r\n            \r\n            .tree-item-content {\r\n                color: var(--color-primary);\r\n                font-weight: 600;\r\n            }\r\n\r\n            &::before {\r\n                content: '';\r\n                position: absolute;\r\n                left: 0;\r\n                top: 0;\r\n                bottom: 0;\r\n                width: 3px;\r\n                background: var(--color-primary);\r\n            }\r\n        }\r\n\r\n        .tree-item-content {\r\n            display: flex;\r\n            align-items: center;\r\n            gap: var(--spacing-sm);\r\n            padding: 6px var(--spacing-md);\r\n            color: var(--color-text);\r\n\r\n            .expand-icon {\r\n                width: 12px;\r\n                font-size: 10px;\r\n                color: var(--color-text-secondary);\r\n                transition: transform var(--duration-fast) var(--easing);\r\n            }\r\n\r\n            .item-icon {\r\n                font-size: 14px;\r\n                flex-shrink: 0;\r\n            }\r\n\r\n            .item-name {\r\n                flex: 1;\r\n                overflow: hidden;\r\n                text-overflow: ellipsis;\r\n                white-space: nowrap;\r\n            }\r\n\r\n            .item-size {\r\n                font-size: 11px;\r\n                color: var(--color-text-secondary);\r\n                font-weight: 400;\r\n            }\r\n        }\r\n\r\n        // Children container\r\n        .tree-children {\r\n            overflow: hidden;\r\n            transition: height var(--duration-normal) var(--easing);\r\n        }\r\n\r\n        // Depth styling\r\n        &.depth-1 .tree-item-content { padding-left: 40px; }\r\n        &.depth-2 .tree-item-content { padding-left: 60px; }\r\n        &.depth-3 .tree-item-content { padding-left: 80px; }\r\n    }\r\n}\r\n\r\n// Content Display Area\r\n.content-display {\r\n    flex: 1;\r\n    display: flex;\r\n    flex-direction: column;\r\n    overflow: hidden;\r\n\r\n    .content-header {\r\n        height: 40px;\r\n        background: rgba(22, 27, 34, 0.8);\r\n        border-bottom: 1px solid rgba(0, 255, 65, 0.3);\r\n        display: flex;\r\n        align-items: center;\r\n        padding: 0 var(--spacing-md);\r\n        font-family: var(--font-mono);\r\n        font-size: 13px;\r\n\r\n        #current-path {\r\n            color: var(--color-primary);\r\n            font-weight: 500;\r\n        }\r\n    }\r\n\r\n    .content-body {\r\n        flex: 1;\r\n        overflow-y: auto;\r\n        padding: var(--spacing-lg);\r\n        background: rgba(13, 17, 23, 0.5);\r\n\r\n        &::-webkit-scrollbar {\r\n            width: 8px;\r\n        }\r\n\r\n        &::-webkit-scrollbar-track {\r\n            background: rgba(0, 0, 0, 0.2);\r\n        }\r\n\r\n        &::-webkit-scrollbar-thumb {\r\n            background: var(--color-primary);\r\n            border-radius: 4px;\r\n            \r\n            &:hover {\r\n                background: var(--color-secondary);\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// Content Types\r\n.text-content {\r\n    .terminal-text {\r\n        font-family: var(--font-mono);\r\n        font-size: 14px;\r\n        line-height: 1.6;\r\n        color: var(--color-text);\r\n        white-space: pre-wrap;\r\n        word-wrap: break-word;\r\n    }\r\n}\r\n\r\n.markdown-content {\r\n    font-family: var(--font-primary);\r\n    line-height: 1.6;\r\n    \r\n    h1 {\r\n        font-size: 24px;\r\n        color: var(--color-primary);\r\n        margin-bottom: var(--spacing-lg);\r\n        border-bottom: 2px solid var(--color-primary);\r\n        padding-bottom: var(--spacing-sm);\r\n    }\r\n\r\n    h2 {\r\n        font-size: 20px;\r\n        color: var(--color-secondary);\r\n        margin: var(--spacing-lg) 0 var(--spacing-md) 0;\r\n    }\r\n\r\n    h3 {\r\n        font-size: 16px;\r\n        color: var(--color-accent);\r\n        margin: var(--spacing-md) 0 var(--spacing-sm) 0;\r\n    }\r\n\r\n    p {\r\n        margin-bottom: var(--spacing-md);\r\n        color: var(--color-text);\r\n    }\r\n\r\n    ul, ol {\r\n        margin: var(--spacing-md) 0;\r\n        padding-left: var(--spacing-lg);\r\n        \r\n        li {\r\n            margin-bottom: var(--spacing-sm);\r\n            color: var(--color-text);\r\n        }\r\n    }\r\n\r\n    code {\r\n        background: rgba(0, 255, 65, 0.1);\r\n        color: var(--color-primary);\r\n        padding: 2px 4px;\r\n        border-radius: var(--radius-sm);\r\n        font-family: var(--font-mono);\r\n        font-size: 13px;\r\n    }\r\n\r\n    pre {\r\n        background: rgba(0, 0, 0, 0.4);\r\n        border: 1px solid rgba(0, 255, 65, 0.3);\r\n        border-radius: var(--radius-sm);\r\n        padding: var(--spacing-md);\r\n        margin: var(--spacing-md) 0;\r\n        overflow-x: auto;\r\n        \r\n        code {\r\n            background: none;\r\n            padding: 0;\r\n            color: var(--color-text);\r\n        }\r\n    }\r\n}\r\n\r\n.json-content {\r\n    .json-formatted {\r\n        background: rgba(0, 0, 0, 0.6);\r\n        border: 1px solid var(--color-primary);\r\n        border-radius: var(--radius-sm);\r\n        padding: var(--spacing-lg);\r\n        font-family: var(--font-mono);\r\n        font-size: 13px;\r\n        line-height: 1.5;\r\n        overflow-x: auto;\r\n        color: var(--color-text);\r\n\r\n        .json-key { color: var(--color-secondary); }\r\n        .json-string { color: var(--color-accent); }\r\n        .json-number { color: #FFB800; }\r\n        .json-boolean { color: #FF6B35; }\r\n    }\r\n}\r\n\r\n// Project Content Styling\r\n.project-content {\r\n    .project-header {\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: space-between;\r\n        margin-bottom: var(--spacing-xl);\r\n        padding-bottom: var(--spacing-lg);\r\n        border-bottom: 2px solid var(--color-primary);\r\n\r\n        h1 {\r\n            font-size: 28px;\r\n            color: var(--color-primary);\r\n            margin: 0;\r\n        }\r\n\r\n        .project-status {\r\n            padding: 6px 12px;\r\n            border-radius: var(--radius-sm);\r\n            font-size: 12px;\r\n            font-weight: 600;\r\n            text-transform: uppercase;\r\n            letter-spacing: 0.5px;\r\n\r\n            &.status-completed {\r\n                background: rgba(0, 255, 65, 0.2);\r\n                color: var(--color-success);\r\n                border: 1px solid var(--color-success);\r\n            }\r\n\r\n            &.status-in-progress {\r\n                background: rgba(255, 184, 0, 0.2);\r\n                color: var(--color-warning);\r\n                border: 1px solid var(--color-warning);\r\n            }\r\n\r\n            &.status-maintained {\r\n                background: rgba(57, 255, 20, 0.2);\r\n                color: var(--color-secondary);\r\n                border: 1px solid var(--color-secondary);\r\n            }\r\n\r\n            &.status-archived {\r\n                background: rgba(139, 148, 158, 0.2);\r\n                color: var(--color-text-secondary);\r\n                border: 1px solid var(--color-text-secondary);\r\n            }\r\n        }\r\n    }\r\n\r\n    .project-description {\r\n        margin-bottom: var(--spacing-xl);\r\n\r\n        p {\r\n            font-size: 16px;\r\n            line-height: 1.6;\r\n            margin-bottom: var(--spacing-md);\r\n            color: var(--color-text);\r\n        }\r\n\r\n        .long-description {\r\n            color: var(--color-text-secondary);\r\n            font-size: 14px;\r\n        }\r\n    }\r\n\r\n    .project-technologies {\r\n        margin-bottom: var(--spacing-xl);\r\n\r\n        h3 {\r\n            color: var(--color-secondary);\r\n            margin-bottom: var(--spacing-md);\r\n        }\r\n\r\n        .tech-tags {\r\n            display: flex;\r\n            flex-wrap: wrap;\r\n            gap: var(--spacing-sm);\r\n\r\n            .tech-tag {\r\n                background: rgba(0, 255, 65, 0.1);\r\n                color: var(--color-primary);\r\n                padding: 4px 8px;\r\n                border-radius: var(--radius-sm);\r\n                border: 1px solid rgba(0, 255, 65, 0.3);\r\n                font-size: 12px;\r\n                font-family: var(--font-mono);\r\n                transition: all var(--duration-fast) var(--easing);\r\n\r\n                &:hover {\r\n                    background: rgba(0, 255, 65, 0.2);\r\n                    box-shadow: 0 0 8px rgba(0, 255, 65, 0.3);\r\n                    transform: translateY(-1px);\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .project-features {\r\n        margin-bottom: var(--spacing-xl);\r\n\r\n        h3 {\r\n            color: var(--color-secondary);\r\n            margin-bottom: var(--spacing-md);\r\n        }\r\n\r\n        ul {\r\n            list-style: none;\r\n            padding: 0;\r\n\r\n            li {\r\n                position: relative;\r\n                padding-left: var(--spacing-lg);\r\n                margin-bottom: var(--spacing-sm);\r\n                color: var(--color-text);\r\n\r\n                &::before {\r\n                    content: '▶';\r\n                    position: absolute;\r\n                    left: 0;\r\n                    color: var(--color-primary);\r\n                    font-size: 12px;\r\n                }\r\n\r\n                &:hover::before {\r\n                    color: var(--color-secondary);\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .project-links {\r\n        margin-bottom: var(--spacing-xl);\r\n\r\n        h3 {\r\n            color: var(--color-secondary);\r\n            margin-bottom: var(--spacing-md);\r\n        }\r\n\r\n        .link-buttons {\r\n            display: flex;\r\n            gap: var(--spacing-md);\r\n            flex-wrap: wrap;\r\n\r\n            .link-btn {\r\n                display: inline-flex;\r\n                align-items: center;\r\n                padding: 8px 16px;\r\n                border-radius: var(--radius-sm);\r\n                text-decoration: none;\r\n                font-size: 14px;\r\n                font-weight: 500;\r\n                transition: all var(--duration-normal) var(--easing);\r\n                border: 1px solid transparent;\r\n\r\n                &.github {\r\n                    background: rgba(0, 255, 65, 0.1);\r\n                    color: var(--color-primary);\r\n                    border-color: var(--color-primary);\r\n\r\n                    &:hover {\r\n                        background: rgba(0, 255, 65, 0.2);\r\n                        box-shadow: var(--shadow-glow);\r\n                        transform: translateY(-2px);\r\n                    }\r\n                }\r\n\r\n                &.demo {\r\n                    background: rgba(255, 107, 53, 0.1);\r\n                    color: var(--color-accent);\r\n                    border-color: var(--color-accent);\r\n\r\n                    &:hover {\r\n                        background: rgba(255, 107, 53, 0.2);\r\n                        box-shadow: 0 0 20px rgba(255, 107, 53, 0.3);\r\n                        transform: translateY(-2px);\r\n                    }\r\n                }\r\n\r\n                &.docs {\r\n                    background: rgba(57, 255, 20, 0.1);\r\n                    color: var(--color-secondary);\r\n                    border-color: var(--color-secondary);\r\n\r\n                    &:hover {\r\n                        background: rgba(57, 255, 20, 0.2);\r\n                        box-shadow: 0 0 20px rgba(57, 255, 20, 0.3);\r\n                        transform: translateY(-2px);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .project-achievements {\r\n        margin-bottom: var(--spacing-xl);\r\n\r\n        h3 {\r\n            color: var(--color-secondary);\r\n            margin-bottom: var(--spacing-md);\r\n        }\r\n\r\n        .achievements-list {\r\n            list-style: none;\r\n            padding: 0;\r\n\r\n            li {\r\n                background: rgba(0, 255, 65, 0.05);\r\n                border: 1px solid rgba(0, 255, 65, 0.2);\r\n                border-radius: var(--radius-sm);\r\n                padding: var(--spacing-sm) var(--spacing-md);\r\n                margin-bottom: var(--spacing-sm);\r\n                color: var(--color-text);\r\n                transition: all var(--duration-fast) var(--easing);\r\n\r\n                &:hover {\r\n                    background: rgba(0, 255, 65, 0.1);\r\n                    border-color: rgba(0, 255, 65, 0.4);\r\n                    transform: translateX(4px);\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .project-metadata {\r\n        display: flex;\r\n        gap: var(--spacing-xl);\r\n        padding: var(--spacing-lg);\r\n        background: rgba(0, 0, 0, 0.3);\r\n        border-radius: var(--radius-sm);\r\n        border: 1px solid rgba(0, 255, 65, 0.2);\r\n\r\n        .metadata-item {\r\n            display: flex;\r\n            flex-direction: column;\r\n            gap: var(--spacing-xs);\r\n\r\n            .label {\r\n                font-size: 12px;\r\n                color: var(--color-text-secondary);\r\n                text-transform: uppercase;\r\n                letter-spacing: 0.5px;\r\n            }\r\n\r\n            .value {\r\n                font-size: 14px;\r\n                color: var(--color-primary);\r\n                font-weight: 500;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// Skills Content Styling\r\n.skills-content {\r\n    h1 {\r\n        font-size: 28px;\r\n        color: var(--color-primary);\r\n        margin-bottom: var(--spacing-xl);\r\n        text-align: center;\r\n        border-bottom: 2px solid var(--color-primary);\r\n        padding-bottom: var(--spacing-lg);\r\n    }\r\n\r\n    .skill-category {\r\n        margin-bottom: var(--spacing-xxl);\r\n\r\n        h2 {\r\n            font-size: 20px;\r\n            color: var(--color-secondary);\r\n            margin-bottom: var(--spacing-lg);\r\n            padding-left: var(--spacing-md);\r\n            border-left: 3px solid var(--color-secondary);\r\n        }\r\n\r\n        .skills-grid {\r\n            display: grid;\r\n            grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\r\n            gap: var(--spacing-lg);\r\n\r\n            .skill-item {\r\n                background: rgba(0, 0, 0, 0.4);\r\n                border: 1px solid rgba(0, 255, 65, 0.2);\r\n                border-radius: var(--radius-sm);\r\n                padding: var(--spacing-lg);\r\n                transition: all var(--duration-normal) var(--easing);\r\n\r\n                &:hover {\r\n                    border-color: rgba(0, 255, 65, 0.5);\r\n                    background: rgba(0, 255, 65, 0.05);\r\n                    transform: translateY(-4px);\r\n                    box-shadow: var(--shadow-elevation);\r\n                }\r\n\r\n                .skill-header {\r\n                    display: flex;\r\n                    justify-content: space-between;\r\n                    align-items: center;\r\n                    margin-bottom: var(--spacing-md);\r\n\r\n                    .skill-name {\r\n                        font-size: 16px;\r\n                        font-weight: 600;\r\n                        color: var(--color-primary);\r\n                    }\r\n\r\n                    .skill-experience {\r\n                        font-size: 12px;\r\n                        color: var(--color-text-secondary);\r\n                        background: rgba(0, 255, 65, 0.1);\r\n                        padding: 2px 6px;\r\n                        border-radius: var(--radius-sm);\r\n                    }\r\n                }\r\n\r\n                .skill-proficiency {\r\n                    display: flex;\r\n                    align-items: center;\r\n                    gap: var(--spacing-md);\r\n                    margin-bottom: var(--spacing-md);\r\n\r\n                    .proficiency-bar {\r\n                        display: flex;\r\n                        gap: 2px;\r\n\r\n                        .proficiency-dot {\r\n                            width: 8px;\r\n                            height: 8px;\r\n                            border-radius: 50%;\r\n                            background: rgba(0, 255, 65, 0.2);\r\n                            transition: all var(--duration-fast) var(--easing);\r\n\r\n                            &.filled {\r\n                                background: var(--color-primary);\r\n                                box-shadow: 0 0 4px rgba(0, 255, 65, 0.5);\r\n                            }\r\n                        }\r\n                    }\r\n\r\n                    .proficiency-level {\r\n                        font-size: 12px;\r\n                        color: var(--color-text-secondary);\r\n                        font-weight: 500;\r\n                    }\r\n                }\r\n\r\n                .skill-description {\r\n                    font-size: 13px;\r\n                    color: var(--color-text);\r\n                    line-height: 1.5;\r\n                    margin-bottom: var(--spacing-sm);\r\n                }\r\n\r\n                .skill-certifications {\r\n                  